import React, { useState } from "react"
import {
  TextInput,
  createStyles,
  rem,
  Title,
  ScrollArea,
  SimpleGrid,
  Image,
} from "@mantine/core"
import { IconSearch } from "@tabler/icons-react"
import { FeaturesCard } from "./FeaturesCard"
import { TItem, useShopData } from "../../providers/ShopDataProvider"
import { AnimatePresence, motion } from "framer-motion"
import { useGeneralStyles } from "../../theme"
import { useLocales } from "../../providers/LocaleProvider"
const useStyles = createStyles((theme) => ({
  aside: {
    backgroundColor:
      theme.colorScheme === "dark"
        ? theme.colors.dark[8]
        : theme.colors.gray[1],
    borderRight: `${rem(1)} solid ${
      theme.colorScheme === "dark" ? theme.colors.dark[8] : theme.colors.gray[3]
    }`,
  },

  shopTitleHeaderSearchBar: {
    backgroundColor:
      theme.colorScheme === "dark"
        ? theme.colors.dark[7]
        : theme.colors.gray[0],
    borderBottom: `${rem(1)} solid ${
      theme.colorScheme === "dark" ? theme.colors.dark[6] : theme.colors.gray[2]
    }`,
  },
}))
function Left() {
  const { classes } = useStyles()
  const myClass = useGeneralStyles()
  const { shopItems, shopName, shopLogo, player } = useShopData()
  const [searchPhrase, setSearchPhrase] = useState("")
  const [animationCompleted, setAnimationCompleted] = useState(false)
  const { locale } = useLocales()

  const handleSearchChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const value = e.target.value
    setSearchPhrase(value)
  }

  return (
    <div className="h-full w-2/3 flex flex-col ">
      <div
        className={`${classes.shopTitleHeaderSearchBar} h-[4rem] w-full flex flex-row items-center justify-between pl-6 pr-6 rounded-tl-sm`}
      >
        <div className="flex flex-row gap-3 h-full justify-center items-center">
          <Image src={shopLogo ? shopLogo : "https://raw.githubusercontent.com/Q4D1952K/QBoxRpImg/main/247.png"} height={"3rem"} fit="contain" />
          <Title
            order={4}
            className={`${myClass.classes.title} tracking-tight flex-nowrap whitespace-nowrap`}
          >
            {shopName ? shopName : "Circle 24/7"}
          </Title>
        </div>
        <div className="flex-1 max-w-md">
          <TextInput
            placeholder={locale.ui.search_placeholder}
            size="xs"
            icon={<IconSearch size="0.8rem" stroke={1.5} />}
            rightSectionWidth={70}
            onChange={handleSearchChange}
          />
        </div>
      </div>

      <ScrollArea
        className={`${classes.aside} flex-1 p-6 rounded-bl-sm`}
        offsetScrollbars
        scrollbarSize={3}
      >
        <SimpleGrid cols={3}>
          <AnimatePresence>
            {shopItems &&
              shopItems
                .filter((item: TItem) => {
                  return (
                    item.label &&
                    item.label
                      .toLowerCase()
                      .includes(searchPhrase.toLowerCase())
                  )
                })
                .map(
                  (item: TItem, index) =>
                    item.label && (
                      <motion.div
                        onAnimationComplete={() => {
                          setAnimationCompleted(true)
                        }}
                        key={item.name}
                        initial={{
                          opacity: animationCompleted ? 1 : 0,
                          y: animationCompleted ? 1 : 50,
                        }}
                        animate={{ opacity: 1, y: 0 }}
                        transition={{
                          duration: 0.2,
                          delay: index * 0.1,
                          type: "tween",
                        }}
                      >
                        <FeaturesCard item={item} />
                      </motion.div>
                    )
                )}
          </AnimatePresence>
        </SimpleGrid>
      </ScrollArea>
    </div>
  )
}

export default Left
