import {
  Center,
  Text,
  createStyles,
  SimpleGrid,
  ScrollArea,
  rem,
  Button,
  Title,
  ActionIcon,
} from "@mantine/core"
import {
  IconCash,
  IconCreditCard,
  IconWeight,
  IconX,
} from "@tabler/icons-react"
import { CartItem } from "./CartItem"
import { useGeneralStyles } from "../../theme"
import { useShopData } from "../../providers/ShopDataProvider"
import { USDollar } from "../../utils/misc"
import ScaleFade from "../../transitions/ScaleFade"
import { AnimatePresence, motion } from "framer-motion"
import { fetchNui } from "../../utils/fetchNui"
import { useLocales } from "../../providers/LocaleProvider"

const useStyles = createStyles((theme) => ({
  aside: {
    backgroundColor:
      theme.colorScheme === "dark" ? theme.colors.dark[8] : theme.white,
    borderRight: `${rem(1)} solid ${
      theme.colorScheme === "dark" ? theme.colors.dark[8] : theme.colors.gray[3]
    }`,
    borderBottom: `${rem(1)} solid ${
      theme.colorScheme === "dark" ? theme.colors.dark[6] : theme.colors.gray[2]
    }`,
  },

  main: {
    backgroundColor:
      theme.colorScheme === "dark"
        ? theme.colors.dark[9]
        : theme.colors.gray[0],
  },
}))

function Right() {
  const { classes: mainClasses } = useStyles()
  const { classes } = useGeneralStyles()
  const { cart, player, shopId, closeShop } = useShopData()
  const { locale } = useLocales()

  const total = cart
    .map((cartItem) => (cartItem.quantity ?? 0) * cartItem.price)
    .reduce((acc, curr) => acc + curr, 0)

  const formattedTotal = USDollar.format(total)

  const paymentButtonHandler = (method: string) => {
    const dataSending = { total, method, items: cart, id: shopId }
    fetchNui("payment", dataSending).then((data) => {
      if (data) {
        closeShop()
      } else return
    })
  }
  return (
    <div
      className={`${mainClasses.main} h-full w-1/3 flex flex-col rounded-br-sm rounded-tr-sm`}
    >
      <div
        className={`${mainClasses.aside} h-[4rem] w-full flex flex-row items-center justify-between p-6`}
      >
        <div className={` flex flex-row gap-4`}>
          <Center className="weight flex flex-row gap-1">
            <Text fz="sm" className={classes.label} span>
              {((player?.weights ?? 0) / 1000).toFixed(2)}{" "}
            </Text>
            <Text fz="sm" span>
              {" "}
              /{" "}
            </Text>
            <Text fz="sm" className={classes.label} span c="dimmed">
              {((player?.maxWeights ?? 0) / 1000).toFixed(2)}{" "}
            </Text>
            <IconWeight
              className={`${classes.icon} ${classes.weightIconColor}`}
              size="1.05rem"
              stroke={1.5}
            />
          </Center>

          <Center className="weight flex flex-row gap-1">
            <Text fz="sm" span className={classes.label}>
              {player?.cash}
            </Text>
            <IconCash
              className={`${classes.icon} ${classes.cashIconColor}`}
              size="1.05rem"
              stroke={1.5}
            />
          </Center>

          <Center className="weight flex flex-row gap-1">
            <Text fz="sm" span className={classes.label}>
              {player?.bank}
            </Text>
            <IconCreditCard
              className={`${classes.icon} ${classes.cardIconColor}`}
              size="1.05rem"
              stroke={1.5}
            />
          </Center>
        </div>
        <ActionIcon
          onClick={() => {
            closeShop()
          }}
        >
          <IconX className={`${classes.link} `} />
        </ActionIcon>
      </div>

      <ScrollArea className="h-full p-6" offsetScrollbars scrollbarSize={4}>
        <SimpleGrid cols={1}>
          <AnimatePresence mode="popLayout">
            {cart.map((item) => (
              <motion.div
                layout
                initial={{ opacity: 0.9, y: -100 }}
                animate={{ scale: 1, opacity: 1, y: 0 }}
                exit={{ scale: 0.8, opacity: 0 }}
                transition={{ type: "tween" }}
                key={item.name}
              >
                <ScaleFade visible={true}>
                  <CartItem item={item} />
                </ScaleFade>
              </motion.div>
            ))}
          </AnimatePresence>
        </SimpleGrid>
      </ScrollArea>

      <div className="pl-6 pr-6 pb-6 flex flex-col gap-2 text-right">
        <Title order={5} className={`${classes.title} tracking-tight`}>
          {locale.ui.total}{" "}
          <span className={`${classes.cashIconColor}`}>${formattedTotal}</span>
        </Title>
        <Button
          disabled={cart.length === 0}
          fullWidth
          leftIcon={<IconCash />}
          onClick={() => paymentButtonHandler("cash")}
        >
          {locale.ui.pay_cash}
        </Button>
        <Button
          disabled={cart.length === 0}
          fullWidth
          leftIcon={<IconCreditCard />}
          onClick={() => paymentButtonHandler("card")}
        >
          {locale.ui.pay_card}
        </Button>
      </div>
    </div>
  )
}

export default Right
