import { Context, createContext, useContext, useState } from "react"
import { useNuiEvent } from "../hooks/useNuiEvent"
import { debugData } from "../utils/debugData"

debugData([
  {
    action: "loadLocales",
    data: ["en", "vi"],
  },
])

debugData([
  {
    action: "setLocale",
    data: {
      language: "Ting Vit",
      ui: {
        search_placeholder: "Tm kim...",
        add_to_cart: "Thm vo gi",
        remove_from_cart: "Xa",
        pay_cash: "Tr tin mt",
        pay_card: "Tr th",
        total: "Tng:",
      },
    },
  },
])

interface Locale {
  language: string
  ui: {
    search_placeholder: string
    add_to_cart: string
    remove_from_cart: string
    pay_cash: string
    pay_card: string
    total: string
  }
}

interface LocaleContextValue {
  locale: Locale
  setLocale: (locales: Locale) => void
}

const LocaleCtx = createContext<LocaleContextValue | null>(null)

const LocaleProvider: React.FC<{ children: React.ReactNode }> = ({
  children,
}) => {
  const [locale, setLocale] = useState<Locale>({
    language: "",
    ui: {
      search_placeholder: "",
      add_to_cart: "",
      remove_from_cart: "",
      pay_cash: "",
      pay_card: "",
      total: "",
    },
  })

  useNuiEvent("setLocale", async (data: Locale) => setLocale(data))

  return (
    <LocaleCtx.Provider value={{ locale, setLocale }}>
      {children}
    </LocaleCtx.Provider>
  )
}

export default LocaleProvider

export const useLocales = () =>
  useContext<LocaleContextValue>(LocaleCtx as Context<LocaleContextValue>)
