import { Card, Image, Title, Text, Center, Button } from "@mantine/core"
import { IconCoins, IconShoppingCartOff, IconWeight } from "@tabler/icons-react"
import { QuantityInput } from "./QuantityInputs"
import { useGeneralStyles } from "../../theme"
import { TItem, useShopData } from "../../providers/ShopDataProvider"
import { USDollar } from "../../utils/misc"
import { useLocales } from "../../providers/LocaleProvider"

export const CartItem: React.FC<{ item: TItem }> = ({ item }) => {
  const { classes } = useGeneralStyles()

  const { cart, setCart } = useShopData()
  const handleRemoveFromCart = (item: TItem) => {
    const updatedCart = cart.filter((cartItem) => cartItem.name !== item.name)
    setCart(updatedCart)
  }
  const { locale } = useLocales()

  return (
    <Card className={`${classes.card} flex flex-row justify-between`}>
      <Card.Section
        className={`${classes.rightCardImageSection} ${classes.imageSection} flex justify-center items-center w-1/3`}
      >
        <Image
          src={item.image}
          alt={item.name}
          height={"100px"}
          width={"100px"}
        />
      </Card.Section>

      <div className="flex-1 ml-8 flex flex-col gap-3">
        <div>
          <Title order={5} className={`${classes.title} tracking-tight`}>
            {item.label}
          </Title>

          <div className="flex flex-row gap-2">
            <Center className="">
              <Text
                fz="xs"
                className={`${classes.label} ${classes.labelMarginRightToIcon}`}
              >
                {USDollar.format(item.price * (item.quantity ?? 1))}
              </Text>
              <IconCoins
                size="0.95rem"
                className={`${classes.icon} ${classes.moneyIconColor}`}
              />
            </Center>
            <Center>
              <Text
                fz="xs"
                className={`${classes.label} ${classes.labelMarginRightToIcon}`}
              >
                {(((item.weight ?? 0) / 1000) * (item.quantity ?? 1)).toFixed(
                  2
                )}
              </Text>
              <IconWeight
                className={`${classes.icon} ${classes.weightIconColor}`}
                size="0.95rem"
              />
            </Center>
          </div>
        </div>
        <div className="flex flex-col gap-1.5">
          <div className=" w-full h-12 flex flex-row justify-between">
            <QuantityInput item={item} />
          </div>
          <div className="w-full h-12">
            <Button
              fullWidth
              size="sm"
              onClick={() => handleRemoveFromCart(item)}
              leftIcon={<IconShoppingCartOff />}
              className={`${classes.largeButtons} h-full`}
            >
              {locale.ui.remove_from_cart}
            </Button>
          </div>
        </div>
      </div>
    </Card>
  )
}
