import { Card, Image, Title, Text, Center, Button, Box } from "@mantine/core"
import {
  IconPackage,
  IconShoppingCartPlus,
  IconWeight,
} from "@tabler/icons-react"
import { useGeneralStyles } from "../../theme"
import { TItem, useShopData } from "../../providers/ShopDataProvider"
import { USDollar } from "../../utils/misc"
import { useState } from "react"
import ScaleFade from "../transitions/ScaleFade"
import { useLocales } from "../../providers/LocaleProvider"

export const FeaturesCard: React.FC<{ item: TItem }> = ({ item }) => {
  const { classes } = useGeneralStyles()
  const { cart, setCart } = useShopData()
  const existingItem = cart.find((cartItem) => cartItem.name === item.name)
  const handleAddToCart = (item: TItem) => {
    if (existingItem) {
      const updatedCart = cart.map((cartItem) =>
        cartItem.name === item.name
          ? { ...cartItem, quantity: (cartItem.quantity || 0) + 1 }
          : cartItem
      )
      setCart(updatedCart)
    } else {
      setCart([...cart, { ...item, quantity: 1 }])
    }
  }
  const { locale } = useLocales()
  return (
    <Card className={`${classes.card} `}>
      <Card.Section className={`${classes.cardTop}  pb-3`}>
        <Box className="flex flex-col items-start gap-1 absolute">
          <Center className="flex flex-row items-center gap-2">
            <IconWeight
              className={` ${classes.weightIconColor}`}
              size="1.05rem"
              stroke={1.5}
            />
            <Text size="xs" fw={500}>
              {((item.weight ?? 0) / 1000).toFixed(2)}{" "}
            </Text>
          </Center>
          <Center className="flex flex-row items-center gap-2">
            <IconPackage
              className={`${classes.weightIconColor}`}
              size="1.05rem"
              stroke={1.5}
            />
            <Text size="xs" fw={500}>
              {item.amount}
            </Text>
          </Center>
        </Box>

        <Center className="relative">
          <Image
            src={item.image}
            alt={item.name}
            height={"100px"}
            width={"100px"}
          />
        </Center>
      </Card.Section>

      <Box className="flex flex-col gap-2 mt-3 justify-between">
        <Title order={5} truncate className={`${classes.title} tracking-tight`}>
          {item.label}
        </Title>

        <Text color="dimmed" size="xs" lineClamp={2}>
          {item.description
            ? item.description
            : "This item does not have a description. Sounds like a scam.."}
        </Text>
        <Center className="flex flex-row gap-1 mt-1 justify-between">
          <Title
            className={`${classes.cashIconColor} tracking-wide mt-1`}
            order={5}
          >
            ${USDollar.format(item.price)}
          </Title>
          <Button
            onClick={() => {
              handleAddToCart(item)
            }}
            size="xs"
            leftIcon={<IconShoppingCartPlus size="1.25rem" />}
            className={`${classes.largeButtons} `}
            disabled={
              existingItem?.quantity !== undefined &&
              existingItem.quantity >= item.amount
            }
          >
            {locale.ui.add_to_cart}
          </Button>
        </Center>
      </Box>
    </Card>
  )
}
