import { useRef } from "react"
import {
  createStyles,
  NumberInput,
  NumberInputHandlers,
  ActionIcon,
  rem,
} from "@mantine/core"
import { IconPlus, IconMinus } from "@tabler/icons-react"
import { TItem, useShopData } from "../../providers/ShopDataProvider"

const useStyles = createStyles((theme) => ({
  wrapper: {
    width: "100%",
    display: "flex",
    alignItems: "center",
    justifyContent: "space-between",
    padding: `${rem(6)} ${theme.spacing.xs}`,
    borderRadius: theme.radius.xs,
    border: `${rem(1)} solid ${
      theme.colorScheme === "dark" ? "transparent" : theme.colors.gray[3]
    }`,
    backgroundColor:
      theme.colorScheme === "dark" ? theme.colors.dark[6] : theme.white,

    "&:focus-within": {
      borderColor: theme.colors[theme.primaryColor][6],
    },
  },

  control: {
    color: theme.colorScheme === "dark" ? theme.colors.dark[1] : theme.black,
    backgroundColor:
      theme.colorScheme === "dark" ? theme.colors.dark[7] : theme.white,
    border: `${rem(1)} solid ${
      theme.colorScheme === "dark" ? "transparent" : theme.colors.gray[3]
    }`,

    "&:disabled": {
      color:
        theme.colorScheme === "dark"
          ? theme.colors.dark[3]
          : theme.colors.gray[3],
      borderColor:
        theme.colorScheme === "dark" ? "transparent" : theme.colors.gray[3],
      opacity: 0.8,
      backgroundColor:
        theme.colorScheme === "dark" ? theme.colors.dark[5] : "transparent",
    },
  },

  input: {
    textAlign: "center",
    paddingRight: `${theme.spacing.sm} !important`,
    paddingLeft: `${theme.spacing.sm} !important`,
    height: rem(28),
    flex: 1,
  },
}))

interface QuantityInputProps {
  item: TItem
  min?: number
}
export function QuantityInput({ min = 1, item }: QuantityInputProps) {
  const { classes } = useStyles()
  const handlers = useRef<NumberInputHandlers>(null)
  const { cart, setCart } = useShopData()
  const handleChange = (newValue: number | "") => {
    // Check if the input value is a valid number before updating the state
    if (typeof newValue === "number" && !isNaN(newValue)) {
      item.quantity = newValue // Update the item.quantity directly
      // I think we need to setCart here
      setCart([...cart])
    }
  }

  return (
    <div className={classes.wrapper}>
      <ActionIcon<"button">
        size={28}
        variant="transparent"
        onClick={() => handlers.current?.decrement()}
        disabled={item.quantity === min} // Use item.quantity here
        className={classes.control}
        onMouseDown={(event) => event.preventDefault()}
      >
        <IconMinus size="1rem" stroke={1.5} />
      </ActionIcon>

      <NumberInput
        variant="unstyled"
        min={min}
        max={item.amount}
        handlersRef={handlers}
        value={item.quantity} // Use item.quantity here as the initial value
        onChange={handleChange}
        classNames={{ input: classes.input }}
      />

      <ActionIcon<"button">
        size={28}
        variant="transparent"
        disabled={item.quantity === item.amount}
        onClick={() => handlers.current?.increment()}
        className={classes.control}
        onMouseDown={(event) => event.preventDefault()}
      >
        <IconPlus size="1rem" stroke={1.5} />
      </ActionIcon>
    </div>
  )
}
